import axiosStatic, { AxiosInstance } from 'axios';
import { BehaviorSubject } from 'rxjs';

import { Account } from '../../../api/src/domain/Account';
import { CreateSiteRequest } from '../../../api/src/domain/CreateSite';
import {
  CreateTourRequest,
  CreateTourResponse,
} from '../../../api/src/domain/CreateTour';
import { ListToursResponse } from '../../../api/src/domain/ListTours';
import { Site } from '../../../api/src/domain/Site';
import { Tour } from '../../../api/src/domain/Tour';

export class ManagementService {
  private readonly account$$ = new BehaviorSubject<Account | null | undefined>(
    undefined,
  );
  private readonly sites$$ = new BehaviorSubject<Site[] | undefined>(undefined);
  private readonly tours$$ = new BehaviorSubject<Tour[] | undefined>(undefined);

  public readonly account$ = this.account$$.asObservable();
  public readonly sites$ = this.sites$$.asObservable();
  public readonly tours$ = this.tours$$.asObservable();

  constructor(
    private readonly axios: AxiosInstance = axiosStatic.create({
      baseURL: `${
        new URLSearchParams(window.location.search.slice(1)).get('_devHost') ??
        `https://api.${window.location.hostname.replace('www.', '')}`
      }/api/v1/`,
      withCredentials: true,
    }),
  ) {}

  public async init(): Promise<void> {
    const before = this.account$$.value;
    const after = await this.getMe().catch(() => null);
    if (before?.id !== after?.id) {
      if (after === null) {
        this.sites$$.next(undefined);
      } else {
        await this.loadSites(after);
      }
    }
    this.account$$.next(after);
  }

  public async getMe(): Promise<Account> {
    return (await this.axios.get<{ account: Account }>('management/me')).data
      .account;
  }

  public async signUp(email: string): Promise<Account> {
    const { data: account } = await this.axios.post<Account>(
      'management/signup',
      {
        email,
        id: '',
      },
    );

    this.account$$.next(account);
    return account;
  }

  public async login(accountId: string, code: string): Promise<Account> {
    const response = await this.axios.post<Account>('management/login', {
      code,
      id: accountId,
    });
    const account = response.data;

    this.account$$.next(account);
    await this.loadSites(account);

    return account;
  }

  public async logout(): Promise<void> {
    await this.axios.post<Account>('management/logout');
    this.tours$$.next(undefined);
    this.sites$$.next(undefined);
    this.account$$.next(undefined);
    window.location.reload();
  }

  public async loadTours(siteId: string): Promise<Tour[]> {
    const response = await this.axios.get<ListToursResponse>(
      `management/sites/${siteId}/tours`,
    );

    this.tours$$.next(response.data.tours);
    return response.data.tours;
  }

  public async createTour(
    siteId: string,
    req: CreateTourRequest,
  ): Promise<Tour> {
    const response = await this.axios.post<CreateTourResponse>(
      `management/sites/${siteId}/tours`,
      req,
    );
    const tour = response.data.tour;
    const tours = this.tours$$.value;
    this.tours$$.next(tours === undefined ? [tour] : tours.concat([tour]));
    return tour;
  }

  public async updateTour(
    siteId: string,
    tourId: string,
    req: CreateTourRequest,
  ): Promise<Tour> {
    const response = await this.axios.put<CreateTourResponse>(
      `management/sites/${siteId}/tours/${tourId}`,
      req,
    );
    const tour = response.data.tour;
    const tours = this.tours$$.value;
    this.tours$$.next(
      tours === undefined
        ? [tour]
        : tours.map((t) => (t.id === tour.id ? tour : t)),
    );
    return tour;
  }

  private async loadSites(account: Account): Promise<Site[]> {
    const response = await this.axios.get<{ sites: Site[] }>(
      'management/sites',
    );
    const sites = response.data.sites;

    if (sites.length === 0) {
      const site = await this.createSite({ domain: account.email, id: '' });
      sites.push(site);
    }

    this.sites$$.next(sites);
    return sites;
  }

  private async createSite(req: CreateSiteRequest): Promise<Site> {
    const response = await this.axios.post<{ site: Site }>(
      'management/sites',
      req,
    );
    return response.data.site;
  }
}
