import { Box, ButtonGroup, styled } from '@material-ui/core';
import { Field, Form, Formik } from 'formik';
import { TextField } from 'formik-material-ui';

import { Account } from '../../../../api/src/domain/Account';
import { ManagementService } from '../../services/ManagementService';
import yup from '../../validation/yup';
import { Button } from '../Button';

export type CodeFormProps = {
  account: Account | undefined;
  managementService: ManagementService;
  onCancel: () => void;
  onSignIn?: (account: Account) => void;
};

const codeSchema = yup.object().shape({
  code: yup
    .string()
    .required('A 6-digit code is required')
    .matches(/[0-9]{6}/, { message: 'A valid 6-digit code is required' }),
});

const SignInButtonContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(2),
  textAlign: 'right',
}));

export const CodeForm: React.FC<CodeFormProps> = ({
  account,
  managementService,
  onCancel,
  onSignIn = () => undefined,
}) => {
  return (
    <Formik
      initialValues={{ code: '' }}
      onSubmit={async (values, actions) => {
        if (account !== undefined) {
          actions.setSubmitting(true);
          const signInAccount = await managementService.login(
            account.id,
            values.code,
          );
          actions.setSubmitting(false);
          onSignIn(signInAccount);
        }
      }}
      validationSchema={codeSchema}
    >
      {(formik) => (
        <Form>
          <Field
            component={TextField}
            fullWidth
            helperText="Check your email and copy your 6 digit sign-in code into this field."
            label="Sign-in code"
            name="code"
          />
          <SignInButtonContainer>
            <ButtonGroup>
              <Button onClick={onCancel} size="large">
                Cancel
              </Button>
              <Button
                onClick={() => formik.submitForm()}
                size="large"
                variant="contained"
              >
                Sign In
              </Button>
            </ButtonGroup>
          </SignInButtonContainer>
        </Form>
      )}
    </Formik>
  );
};
