import { FormHelperText, styled } from '@material-ui/core';
import React from 'react';
import ReactQuill from 'react-quill';

export type QuillWrapperProps = {
  errorText?: string;
  onBlur: () => void;
  onChange: (html: string) => void;
  value: string;
};

const Container = styled('div')(({ theme }) => ({
  '& > .quill.error': {
    border: `solid 1px ${theme.palette.error.main}`,
  },
}));

export const QuillWrapper: React.FC<QuillWrapperProps> = ({
  errorText,
  onBlur,
  onChange,
  value,
}) => {
  return (
    <Container>
      <ReactQuill
        className={errorText !== undefined ? 'error' : undefined}
        onBlur={onBlur}
        onChange={onChange}
        value={value}
      />
      {errorText !== undefined ? (
        <FormHelperText error>{errorText}</FormHelperText>
      ) : undefined}
    </Container>
  );
};
