import {
  DialogActions,
  DialogContent,
  DialogTitle,
  Drawer,
  IconButton,
  styled,
  Typography,
} from '@material-ui/core';
import { Close as CloseIcon } from '@material-ui/icons';
import { CSSProperties } from '@material-ui/system';
import React, { ReactElement, ReactNode } from 'react';

export type DialogDrawerProps = {
  actions: ReactElement;
  children: ReactNode;
  onClose?: () => void;
  onExited?: () => void;
  open: boolean;
  title: ReactNode;
  width?: CSSProperties['width'];
};

const DDTitle = styled(DialogTitle)(({ theme }) => ({
  alignItems: 'center',
  display: 'flex',
  justifyContent: 'space-between',
  marginBottom: theme.spacing(4),
}));

const DDContent = styled(DialogContent)({
  padding: '3em',
});

const DDActions = styled(DialogActions)(({ theme }) => ({
  backgroundColor: theme.palette.grey['50'],
  borderTop: `solid 1px`,
  borderTopColor: theme.palette.grey['200'],
  padding: theme.spacing(4),
}));

export const DialogDrawer: React.FC<DialogDrawerProps> = ({
  actions,
  children,
  onClose,
  onExited,
  open,
  title,
  width = '35rem',
}) => {
  return (
    <Drawer
      anchor="right"
      open={open}
      onClose={onClose}
      SlideProps={{ onExited }}
    >
      <DDTitle>
        <Typography component="span" variant="h5" style={{ flexGrow: 1 }}>
          {title}
        </Typography>
        {onClose !== undefined ? (
          <IconButton onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : undefined}
      </DDTitle>
      <DDContent sx={{ width }}>{children}</DDContent>
      <DDActions>{actions}</DDActions>
    </Drawer>
  );
};
