import { Avatar, Box, Card, CardHeader, styled } from '@material-ui/core';
import { Map } from '@material-ui/icons';
import React from 'react';
import { useParams } from 'react-router-dom';

import { Tour } from '../../../api/src/domain/Tour';
import { useObservable } from '../hooks/useObservable';
import { ManagementService } from '../services/ManagementService';
import { Button } from './Button';
import { EditTourDrawer } from './EditTourDrawer';
import { FeatureContainer } from './FeatureContainer';
import { FirstAction } from './FirstAction';
import { Flex } from './Flex';

export type ToursScreenProps = {
  managementService: ManagementService;
};

const TourCard = styled(Card)(({ theme }) => ({
  '&.published:before': {
    backgroundColor: theme.palette.success.main,
    boxShadow: `0px 0px 5px 1px ${theme.palette.success.main}`,
  },
  '&:before': {
    backgroundColor: theme.palette.grey['300'],
    borderRadius: theme.spacing(1.5),
    content: '" "',
    height: theme.spacing(1.5),
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    width: theme.spacing(1.5),
  },
}));

export const ToursScreen: React.FC<ToursScreenProps> = ({
  managementService,
}) => {
  const { siteId } = useParams<{ siteId: string }>();
  const [tours] = useObservable(
    () => managementService.tours$,
    [managementService],
  );

  const [tourEditOpen, setTourEditOpen] = React.useState(false);
  const [tourEdit, setTourEdit] = React.useState<Tour>();

  React.useEffect(() => {
    void managementService.loadTours(siteId);
  }, [managementService, siteId]);

  return (
    <>
      {(() => {
        if (tours !== undefined && tours.length > 0) {
          return (
            <Box width="100%">
              <Box my={3}>
                <Button
                  color="primary"
                  onClick={() => {
                    setTourEdit(undefined);
                    setTourEditOpen(true);
                  }}
                  variant="contained"
                >
                  Create tour
                </Button>
              </Box>
              <Flex flexWrap="wrap" m={-1}>
                {tours.map((tour) => (
                  <Box key={tour.id} m={1} width="23%">
                    <TourCard
                      className={tour.status}
                      onClick={() => {
                        setTourEdit(tour);
                        setTourEditOpen(true);
                      }}
                      sx={{ cursor: 'pointer', position: 'relative' }}
                    >
                      <CardHeader
                        avatar={
                          <Avatar sx={{ bgcolor: 'blue' }}>
                            {tour.name
                              .split(/\s+/)
                              .map((w) => w[0]?.toUpperCase())
                              .join('')}
                          </Avatar>
                        }
                        title={tour.name}
                      />
                    </TourCard>
                  </Box>
                ))}
              </Flex>
            </Box>
          );
        } else if (tours !== undefined) {
          return (
            <FeatureContainer>
              <FirstAction
                actionsComponent={
                  <Button
                    onClick={() => {
                      setTourEdit(undefined);
                      setTourEditOpen(true);
                    }}
                    size="large"
                    variant="contained"
                  >
                    Create tour
                  </Button>
                }
                illustrationComponent={<Map />}
              >
                Create your first contextual, interactive experience for your
                user that is tailored for your product or website.
              </FirstAction>
            </FeatureContainer>
          );
        }
      })()}
      <EditTourDrawer
        managementService={managementService}
        onClose={() => setTourEditOpen(false)}
        onSave={() => setTourEditOpen(false)}
        open={tourEditOpen}
        siteId={siteId}
        tour={tourEdit}
      />
    </>
  );
};
