import { Box, BoxProps, styled } from '@material-ui/core';
import React, { ReactElement } from 'react';

export type FirstActionProps = BoxProps & {
  actionsComponent?: ReactElement | ReactElement[];
  illustrationComponent: ReactElement | ReactElement[];
};

const Container = styled(Box)({
  alignItems: 'center',
  display: 'flex',
  maxWidth: '35em',
});

const Illustration = styled(Box)(({ fontSize, theme }) => ({
  '& svg': {
    height: theme.typography.h1.fontSize,
    opacity: 0.3,
    width: theme.typography.h1.fontSize,
  },
  marginRight: theme.spacing(2),
}));

const ContentContainer = styled(Box)({
  alignSelf: 'stretch',
  display: 'flex',
  flexDirection: 'column',
  flexGrow: 1,
});

const Content = styled(Box)(({ theme }) => ({
  flexGrow: 1,
  padding: `${theme.spacing(2)} 0`,
}));

const Actions = styled(Box)({
  alignItems: 'center',
  display: 'flex',
  justifyContent: 'flex-end',
});

export const FirstAction: React.FC<FirstActionProps> = ({
  actionsComponent,
  children,
  illustrationComponent,
  ...boxProps
}) => {
  return (
    <Container {...boxProps}>
      <Illustration>{illustrationComponent}</Illustration>
      <ContentContainer>
        <Content>{children}</Content>
        {actionsComponent !== undefined ? (
          <Actions>{actionsComponent}</Actions>
        ) : undefined}
      </ContentContainer>
    </Container>
  );
};
