import { Box, Slide } from '@material-ui/core';
import React from 'react';

import { Account } from '../../../api/src/domain/Account';
import { useObservable } from '../hooks/useObservable';
import { ManagementService } from '../services/ManagementService';
import { FeatureContainer } from './FeatureContainer';
import { CodeForm } from './SignInScreen/CodeForm';
import { EmailForm } from './SignInScreen/EmailForm';

export type SignInScreenProps = {
  managementService: ManagementService;
  onSignIn: () => void;
};

export const SignInScreen: React.FC<SignInScreenProps> = ({
  managementService,
  onSignIn,
}) => {
  const [emailEntered, setEmailEntered] = React.useState(false);
  const [signUpAccount, setSignUpAccount] = React.useState<Account>();
  const [sites] = useObservable(
    () => managementService.sites$,
    [managementService],
  );

  return (
    <FeatureContainer>
      <Slide
        direction={emailEntered ? 'right' : 'left'}
        in={signUpAccount === undefined}
        onEntered={() => {
          setEmailEntered(true);
          focusInput();
        }}
        unmountOnExit
      >
        <Box position="absolute" width={1 / 4}>
          <EmailForm
            managementService={managementService}
            onSignUp={setSignUpAccount}
          />
        </Box>
      </Slide>
      <Slide
        direction={sites === undefined ? 'left' : 'right'}
        in={signUpAccount !== undefined && sites === undefined}
        onEntered={() => focusInput()}
        onExited={() => {
          if (signUpAccount !== undefined && sites !== undefined) {
            onSignIn();
          }
        }}
        unmountOnExit
      >
        <Box position="absolute" width={1 / 4}>
          <CodeForm
            account={signUpAccount}
            managementService={managementService}
            onCancel={() => setSignUpAccount(undefined)}
          />
        </Box>
      </Slide>
    </FeatureContainer>
  );
};

function focusInput(selector = 'input[name]'): void {
  document.querySelector<HTMLInputElement>(selector)?.focus?.();
}
