/* eslint-disable no-console */
import {
  AppBar,
  Box,
  Button,
  CircularProgress,
  Container,
  styled,
  Toolbar,
  Typography,
} from '@material-ui/core';
import BubbleChartIcon from '@material-ui/icons/BubbleChart';
import React from 'react';
import {
  Link as RouterLink,
  Redirect,
  Route,
  Switch,
  useRouteMatch,
} from 'react-router-dom';

import { Site } from '../../../api/src/domain/Site';
import { useObservable } from '../hooks/useObservable';
import { ManagementService } from '../services/ManagementService';
import { AccountDropdown } from './AccountDropdown';
import { SignInScreen } from './SignInScreen';
import { ToursScreen } from './ToursScreen';

const ContentRoot = styled(Container)({
  alignItems: 'center',
  display: 'flex',
  flexDirection: 'column',
  flexGrow: 1,
  width: '100%',
});

const MenuContent = styled(Box)({
  '& > a': {
    border: 'solid 0.3em rgba(0, 0, 0, 0)',
    borderLeftWidth: 0,
    borderRadius: 0,
    borderRightWidth: 0,
    height: '100%',
    padding: '0 2em',
  },
  '& > a.active': {
    borderBottomColor: 'currentColor',
  },
  alignItems: 'center',
  display: 'flex',
  flexGrow: 1,
  height: '100%',
});

const MenuLogo = styled(BubbleChartIcon)(({ theme }) => ({
  height: theme.typography.h5.fontSize,
  marginRight: theme.spacing(1),
  width: theme.typography.h5.fontSize,
}));

const MenuTitle = styled(Typography)(({ theme }) => ({
  marginRight: theme.spacing(4),
}));

const Root = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  height: '100vh',
});

const RootFullScreen = styled(Root)({
  alignItems: 'center',
  justifyContent: 'center',
  width: '100%',
});

const managementService = new ManagementService();

// Add the enactive script so we have enactive integration functionality on our own site
const enactiveScript = document.createElement('script');
enactiveScript.src = `https://${
  window.location.hostname
}/integration/entrypoint.js?_=${Date.now()}`;
enactiveScript.setAttribute(
  'enactive.site-id',
  '007b9009-5f1b-4e70-9c68-165c60003798',
);
enactiveScript.setAttribute('enactive.enabled', 'false');
document.body.appendChild(enactiveScript);

export const AppRoot: React.FC = () => {
  const [account] = useObservable(() => managementService.account$, []);
  const [sites] = useObservable(() => managementService.sites$, []);
  const [site, setSite] = React.useState<Site>();

  const isToursView = useRouteMatch('/sites/:siteId/tours');

  const [waitingForSignIn, setWaitingForSignIn] = React.useState(false);
  const signedIn = React.useMemo(() => {
    if (
      account !== null &&
      account !== undefined &&
      site !== undefined &&
      !waitingForSignIn
    ) {
      return true;
    } else if (account !== undefined) {
      return false;
    } else {
      return undefined;
    }
  }, [account, site, waitingForSignIn]);

  React.useEffect(() => {
    void managementService.init();
  }, []);

  React.useEffect(() => {
    if (account !== undefined) {
      if (account === null) {
        setWaitingForSignIn(true);
        enactiveScript.removeAttribute('enactive.context.user.id');
      } else {
        enactiveScript.setAttribute('enactive.context.user.id', account.id);
      }

      // Don't enable the integration until we have resolved whether or not we are signed in right
      // now
      enactiveScript.setAttribute('enactive.enabled', 'true');
    } else {
      enactiveScript.setAttribute('enactive.enabled', 'false');
    }
  }, [account]);

  React.useEffect(() => {
    if (sites !== undefined) {
      setSite(sites[0]);
    } else {
      setSite(undefined);
    }
  }, [sites]);

  return (
    <>
      {signedIn !== undefined ? (
        <Root>
          <AppBar position="static">
            <Toolbar>
              <MenuLogo />
              <MenuTitle variant="h6">Enactive Management</MenuTitle>
              <MenuContent>
                {(() => {
                  if (signedIn && site !== undefined) {
                    return (
                      <Button
                        className={isToursView !== null ? 'active' : undefined}
                        color="inherit"
                        component={RouterLink}
                        to={`/sites/${site.id}/tours`}
                      >
                        Tours
                      </Button>
                    );
                  }
                })()}
              </MenuContent>
              {(() => {
                if (!signedIn) {
                  return (
                    <Button color="inherit" component={RouterLink} to="/signin">
                      Sign In
                    </Button>
                  );
                } else if (account !== null && account !== undefined) {
                  return (
                    <AccountDropdown
                      account={account}
                      onSignOut={() => managementService.logout()}
                    />
                  );
                }
              })()}
            </Toolbar>
          </AppBar>
          <ContentRoot>
            <Switch>
              <Route exact path="/signin">
                {!signedIn ? (
                  <SignInScreen
                    managementService={managementService}
                    onSignIn={() => setWaitingForSignIn(false)}
                  />
                ) : (
                  <Redirect to="/" />
                )}
              </Route>
              <Route path="/">
                {(() => {
                  if (signedIn && site !== undefined) {
                    return (
                      <Switch>
                        <Route exact path="/">
                          <Redirect to={`/sites/${site.id}/tours`} />
                        </Route>
                        <Route exact path="/sites/:siteId/tours">
                          <ToursScreen managementService={managementService} />
                        </Route>
                      </Switch>
                    );
                  } else {
                    return <Redirect to="/signin" />;
                  }
                })()}
              </Route>
            </Switch>
          </ContentRoot>
        </Root>
      ) : (
        <RootFullScreen>
          <CircularProgress />
        </RootFullScreen>
      )}
    </>
  );
};
