/* eslint-disable no-console */
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  FormControlLabel,
  IconButton,
  styled,
  Switch,
  ToggleButton,
  ToggleButtonGroup,
  useTheme,
} from '@material-ui/core';
import { Box, Typography } from '@material-ui/core';
import {
  Add,
  ArrowDownward,
  ArrowUpward,
  Delete,
  Error,
  ExpandMore,
  SpeakerNotes,
} from '@material-ui/icons';
import {
  Formik,
  Field,
  FieldArray,
  Form,
  FormikProps,
  FormikErrors,
  FieldArrayRenderProps,
} from 'formik';
import { TextField } from 'formik-material-ui';
import React from 'react';
import { SchemaOf } from 'yup';

import { Tour } from '../../../api/src/domain/Tour';
import { TourStatus } from '../../../api/src/domain/TourStatus';
import { TourStep } from '../../../api/src/domain/TourStep';
import { ManagementService } from '../services/ManagementService';
import yup from '../validation/yup';
import { Button } from './Button';
import { DialogDrawer } from './DialogDrawer';
import { FirstAction } from './FirstAction';
import { Flex, FlexItem } from './Flex';
import { QuillWrapper } from './QuillWrapper';

export type EditTourDrawerProps = {
  managementService: ManagementService;
  onClose?: () => void;
  onExited?: () => void;
  onSave?: (tour: Tour) => void;
  open: boolean;
  siteId: string;
  tour?: Tour;
};

const SelectorTypeContainer = styled(FlexItem)(({ theme }) => ({
  margin: theme.spacing(0.5),
}));

const StepAccordionSummary = styled(AccordionSummary)(({ theme }) => ({
  '& > div.MuiAccordionSummary-content': {
    marginRight: theme.spacing(2),
    minWidth: 0,
  },
}));

const selectorSchema: SchemaOf<Required<TourStep['selector']>> = yup
  .object()
  .shape({
    type: yup.mixed<'css' | 'xpath'>().oneOf(['css', 'xpath']).default('css'),
    value: yup
      .string()
      .when('type', {
        is: 'xpath',
        otherwise: yup.string().validCssSelector(),
        then: yup.string().validXpathSelector(),
      })
      .default(''),
  });

const stepSchema: SchemaOf<Omit<TourStep, 'id' | 'buttons'>> = yup
  .object()
  .shape({
    html: yup
      .string()
      .required('Message is required')
      .validNonEmptyHtml({ empty: 'Message requires some text' }),
    mask: yup.boolean().required(),
    selector: selectorSchema.notRequired(),
  });

const tourSchema: SchemaOf<
  Omit<Tour, 'id' | 'steps'> & { steps: Omit<TourStep, 'id' | 'buttons'>[] }
> = yup.object().shape({
  match: yup.object().shape({
    regex: yup
      .string()
      .required('URL match expression is required')
      .validRegex(),
  }),
  name: yup.string().required('Name is required'),
  status: yup
    .mixed<'draft' | 'published'>()
    .oneOf(['draft', 'published'])
    .default('draft'),
  steps: yup.array().of(stepSchema),
});

export const EditTourDrawer: React.FC<EditTourDrawerProps> = ({
  managementService,
  onClose = () => undefined,
  onExited = () => undefined,
  onSave = () => undefined,
  open,
  siteId,
  tour,
}) => {
  const theme = useTheme();
  const formRef = React.useRef<FormikProps<Tour>>(null);
  const [formTour, setFormTour] = React.useState(() => mkTourEdit(tour));

  const [changeStatus, setChangeStatus] = React.useState<TourStatus>();
  const [stepExpansionState, setStepExpansionState] = React.useState<
    Record<number, boolean>
  >({});

  React.useEffect(() => {
    setFormTour(mkTourEdit(tour));
    setStepExpansionState({});
  }, [tour]);

  function handleCreateStep(
    formik: FormikProps<Tour>,
    fieldArray: FieldArrayRenderProps,
  ) {
    setStepExpansionState({
      [formik.values.steps.length]: true,
    });
    fieldArray.push(mkTourEditStep());
  }

  function doSubmit(status?: TourStatus) {
    setChangeStatus(status);
    if (formRef.current !== null) {
      void formRef.current.submitForm();
    }
  }

  return (
    <DialogDrawer
      actions={
        <>
          <Button onClick={() => onClose()} size="large">
            Cancel
          </Button>
          <Button onClick={() => doSubmit('draft')} size="large">
            {formTour?.id === '' || formTour?.status === 'draft'
              ? 'Save draft'
              : 'Save and unpublish'}
          </Button>
          <Button
            onClick={() => doSubmit('published')}
            size="large"
            variant="contained"
          >
            {formTour?.id === ''
              ? 'Publish'
              : formTour?.status === 'draft'
              ? 'Save and publish'
              : 'Save'}
          </Button>
        </>
      }
      onClose={onClose}
      onExited={onExited}
      open={open}
      title={formTour?.id === '' ? 'Create tour' : 'Edit tour'}
    >
      <Formik
        initialValues={formTour}
        innerRef={formRef}
        onSubmit={async (tour, actions) => {
          actions.setSubmitting(true);

          // Clean the tour. Namely, remove selector objects whose value are blank
          const toSave: Tour = {
            ...tour,
            status: changeStatus ?? formTour?.status ?? tour.status,
            steps: tour.steps.map((step) => ({
              ...step,
              selector:
                step.selector !== undefined && step.selector.value.trim() === ''
                  ? undefined
                  : step.selector,
            })),
          };

          const result = await (toSave.id === ''
            ? managementService.createTour(siteId, toSave)
            : managementService.updateTour(siteId, toSave.id, toSave));
          actions.setSubmitting(false);
          onSave(result);
        }}
        validateOnChange={false}
        validationSchema={tourSchema}
      >
        {(formik) => (
          <Form>
            <Typography mb={0.75} component="h2" variant="subtitle1">
              Profile
            </Typography>
            <Field
              component={TextField}
              fullWidth
              label="Name"
              name="name"
              placeholder="New account orientation"
              size="medium"
              validate
              variant="outlined"
            />
            <Typography mb={0.75} mt={2.5} component="h2" variant="subtitle1">
              Criteria
            </Typography>
            <Field
              component={TextField}
              fullWidth
              helperText="Enter a valid regular expression"
              label="URL match expression"
              name="match.regex"
              placeholder="/dashboard/?"
              size="medium"
              validate
              variant="outlined"
            />
            <FieldArray name="steps">
              {(fieldArray) => (
                <>
                  <Typography
                    mb={0.75}
                    mt={2.5}
                    component="h2"
                    variant="subtitle1"
                  >
                    <Flex>
                      <FlexItem grow>Steps</FlexItem>
                      <FlexItem>
                        <IconButton
                          color="primary"
                          onClick={() => handleCreateStep(formik, fieldArray)}
                        >
                          <Add />
                        </IconButton>
                      </FlexItem>
                    </Flex>
                  </Typography>
                  {formik.values.steps.length === 0 ? (
                    <Flex justifyContent="center" width="100%">
                      <FirstAction
                        actionsComponent={
                          <Button
                            color="primary"
                            onClick={() => handleCreateStep(formik, fieldArray)}
                            variant="contained"
                          >
                            Create Step
                          </Button>
                        }
                        illustrationComponent={
                          <SpeakerNotes
                            style={{
                              height: theme.typography.h2.fontSize,
                              width: theme.typography.h2.fontSize,
                            }}
                          />
                        }
                        mt={2}
                        style={{ maxWidth: '30rem' }}
                      >
                        A tour step is one message in a sequence of interactive
                        message dialogs that are shown to your users when the
                        tour criteria is met.
                      </FirstAction>
                    </Flex>
                  ) : (
                    <Box sx={{ width: '100%' }}>
                      {formik.values.steps.map((step, i) => (
                        <Accordion
                          expanded={stepExpansionState[i] ?? false}
                          key={i}
                        >
                          <StepAccordionSummary
                            expandIcon={<ExpandMore />}
                            onClick={() =>
                              setStepExpansionState((ses) => ({ [i]: !ses[i] }))
                            }
                          >
                            <FlexItem width="2rem" mr={1}>
                              <Typography variant="body1" component="h3">
                                {i + 1}.
                              </Typography>
                            </FlexItem>
                            <FlexItem
                              flex="1"
                              minWidth="0"
                              overflow="hidden"
                              textOverflow="ellipsis"
                              whiteSpace="nowrap"
                            >
                              <Typography
                                variant="body2"
                                sx={{
                                  display: 'inline',
                                  opacity: 0.875,
                                }}
                              >
                                {getTextFromHtml(step.html)}
                              </Typography>
                            </FlexItem>
                            {hasInnerStepError(formik.errors, i) ? (
                              <FlexItem
                                color="error.main"
                                mr={1}
                                sx={{ alignItems: 'center', display: 'flex' }}
                              >
                                <Error />
                              </FlexItem>
                            ) : undefined}
                          </StepAccordionSummary>
                          <AccordionDetails>
                            <Flex justifyContent="flex-end">
                              <IconButton
                                disabled={i === 0}
                                onClick={() => {
                                  setStepExpansionState((ses) => ({
                                    ...ses,
                                    [i - 1]: ses[i],
                                    [i]: ses[i - 1],
                                  }));
                                  fieldArray.swap(i - 1, i);
                                }}
                                title="Move up"
                              >
                                <ArrowUpward />
                              </IconButton>
                              <IconButton
                                disabled={i === formik.values.steps.length - 1}
                                onClick={() => {
                                  setStepExpansionState((ses) => ({
                                    ...ses,
                                    [i + 1]: ses[i],
                                    [i]: ses[i + 1],
                                  }));
                                  fieldArray.swap(i, i + 1);
                                }}
                                title="Move down"
                              >
                                <ArrowDownward />
                              </IconButton>
                              <Box ml={2}>
                                <Button
                                  color="inherit"
                                  onClick={() => {
                                    fieldArray.remove(i);
                                    setStepExpansionState((ses) => ({
                                      ...ses,
                                      [i]: false,
                                    }));
                                  }}
                                  startIcon={<Delete />}
                                >
                                  Delete
                                </Button>
                              </Box>
                            </Flex>
                            <Box my={2}>
                              <QuillWrapper
                                errorText={
                                  formik.getFieldMeta(`steps[${i}].html`).error
                                }
                                onBlur={() => {
                                  formik.setFieldTouched(
                                    `steps[${i}].html`,
                                    true,
                                  );
                                }}
                                onChange={(html) => {
                                  formik.setFieldValue(
                                    `steps[${i}].html`,
                                    html,
                                  );
                                }}
                                value={formik.values.steps[i].html}
                              />
                            </Box>
                            <Box my={2}>
                              <FormControlLabel
                                control={
                                  <Switch
                                    checked={formik.values.steps[i].mask}
                                    onChange={() => {
                                      formik.setFieldValue(
                                        `steps[${i}].mask`,
                                        !formik.values.steps[i].mask,
                                      );
                                    }}
                                  />
                                }
                                label="Highlight the message"
                              />
                            </Box>
                            <Flex alignItems="flex-start" my={2}>
                              <FlexItem grow>
                                <Field
                                  component={TextField}
                                  fullWidth
                                  helperText="Use the CSS/XPath toggle to choose a selector format"
                                  label="Highlight element"
                                  name={`steps[${i}].selector.value`}
                                  size="medium"
                                  variant="outlined"
                                />
                              </FlexItem>
                              <SelectorTypeContainer>
                                <ToggleButtonGroup
                                  onChange={() => {
                                    // After the value change happens in the form, set the selector
                                    // field as touched so it revalidates
                                    setTimeout(() =>
                                      formik.setFieldTouched(
                                        `steps[${i}].selector.value`,
                                        true,
                                        true,
                                      ),
                                    );
                                  }}
                                  value={[
                                    formik.values.steps[i].selector?.type ??
                                      'css',
                                  ]}
                                >
                                  <ToggleButton
                                    onClick={() =>
                                      formik.setFieldValue(
                                        `steps[${i}].selector.type`,
                                        'css',
                                      )
                                    }
                                    value="css"
                                  >
                                    CSS
                                  </ToggleButton>
                                  <ToggleButton
                                    onClick={() =>
                                      formik.setFieldValue(
                                        `steps[${i}].selector.type`,
                                        'xpath',
                                      )
                                    }
                                    value="xpath"
                                  >
                                    XPath
                                  </ToggleButton>
                                </ToggleButtonGroup>
                              </SelectorTypeContainer>
                            </Flex>
                          </AccordionDetails>
                        </Accordion>
                      ))}
                    </Box>
                  )}
                </>
              )}
            </FieldArray>
          </Form>
        )}
      </Formik>
    </DialogDrawer>
  );
};

function hasInnerStepError(tour: FormikErrors<Tour>, stepIndex: number) {
  if (tour.steps === undefined || typeof tour.steps === 'string') {
    return false;
  } else {
    // If there is any prop with a value, then it's an error
    const step = tour.steps[stepIndex];
    return (
      step !== undefined &&
      Object.values(step).find((s) => s !== undefined) !== undefined
    );
  }
}

function getTextFromHtml(html: string): string {
  try {
    const text =
      new DOMParser()
        .parseFromString(html, 'text/html')
        ?.body.textContent?.trim() ?? '';
    return text === '' ? 'No content' : text;
  } catch (err) {
    return 'No content';
  }
}

function mkTourEdit(tour?: Tour): Tour {
  if (tour === undefined) {
    return {
      id: '',
      match: {
        regex: '',
      },
      name: '',
      status: 'draft',
      steps: [],
    };
  } else {
    return {
      ...tour,
      steps: tour.steps.map(mkTourEditStep),
    };
  }
}

function mkTourEditStep(step?: TourStep): TourStep {
  if (step === undefined) {
    return {
      html: '',
      id: '',
      mask: false,
      selector: {
        type: 'css',
        value: '',
      },
    };
  } else {
    return step.selector !== undefined
      ? step
      : {
          ...step,
          selector: {
            type: 'css',
            value: '',
          },
        };
  }
}
