import { Box, ButtonGroup, styled } from '@material-ui/core';
import { ArrowForward } from '@material-ui/icons';
import { Field, Form, Formik } from 'formik';
import { TextField } from 'formik-material-ui';

import { Account } from '../../../../api/src/domain/Account';
import { ManagementService } from '../../services/ManagementService';
import yup from '../../validation/yup';
import { Button } from '../Button';

export type EmailFormProps = {
  managementService: ManagementService;
  onSignUp: (account: Account) => void;
};

const signInSchema = yup.object().shape({
  email: yup
    .string()
    .required('Email address is required')
    .email('Invalid email address'),
});

const SignInButtonContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(2),
  textAlign: 'right',
}));

export const EmailForm: React.FC<EmailFormProps> = ({
  managementService,
  onSignUp,
}) => {
  return (
    <Formik
      initialValues={{ email: '' }}
      onSubmit={async (values, actions) => {
        actions.setSubmitting(true);
        const account = await managementService.signUp(values.email);
        actions.setSubmitting(false);
        onSignUp(account);
      }}
      validationSchema={signInSchema}
    >
      {(formik) => (
        <Form>
          <Field
            component={TextField}
            fullWidth
            helperText="We'll send you a sign-in code to this email address."
            label="Email address"
            name="email"
          />
          <SignInButtonContainer>
            <ButtonGroup>
              <Button
                endIcon={<ArrowForward />}
                onClick={() => formik.submitForm()}
                size="large"
                variant="contained"
              >
                Next
              </Button>
            </ButtonGroup>
          </SignInButtonContainer>
        </Form>
      )}
    </Formik>
  );
};
