import { ButtonProps, Button as MuiButton } from '@material-ui/core';
import React from 'react';

export const Button: React.FC<ButtonProps> = ({
  disabled = false,
  key,
  ...buttonProps
}) => {
  const k = React.useMemo(() => {
    // Workaround: https://github.com/mui-org/material-ui/issues/26251
    if (key === undefined && !disabled) {
      return undefined;
    } else if (key === undefined) {
      return 'disabled';
    } else if (!disabled) {
      return key;
    } else {
      return `${key}-disabled`;
    }
  }, [disabled, key]);
  return <MuiButton {...buttonProps} disabled={disabled} key={k} />;
};
