import * as yup from 'yup';

type ValidNonEmptyHtmlOpts = {
  empty?: string;
  invalid?: (err: Error | undefined) => string;
};

yup.addMethod(yup.string, 'validRegex', function () {
  return this.test('test-valid-regex', function (value) {
    if (value !== undefined) {
      const { path, createError } = this;

      try {
        new RegExp(value);
        return true;
      } catch (err) {
        return createError({ message: err.message, path });
      }
    } else {
      return true;
    }
  });
});

yup.addMethod(yup.string, 'validCssSelector', function () {
  return this.test('test-valid-css-selector', function (value) {
    if (value !== undefined && value !== '') {
      const { path, createError } = this;

      try {
        document.querySelector(value);
        return true;
      } catch (err) {
        return createError({ message: 'Invalid CSS selector', path });
      }
    } else {
      return true;
    }
  });
});

yup.addMethod(yup.string, 'validXpathSelector', function () {
  return this.test('test-valid-xpath-selector', function (value) {
    if (value !== undefined && value !== '') {
      const { path, createError } = this;

      try {
        document.evaluate(
          value,
          document,
          null,
          XPathResult.FIRST_ORDERED_NODE_TYPE,
          null,
        );
        return true;
      } catch (err) {
        return createError({ message: 'Invalid XPath selector', path });
      }
    } else {
      return true;
    }
  });
});

yup.addMethod(
  yup.string,
  'validNonEmptyHtml',
  function ({
    empty = 'Please enter some text',
    invalid = (err: Error | undefined) =>
      `Invalid HTML${err !== undefined ? `: ${err.message}` : ''}`,
  }: ValidNonEmptyHtmlOpts = {}) {
    return this.test('test-valid-non-empty-html', function (value) {
      const { path, createError } = this;
      if (value !== undefined) {
        try {
          const parser = new DOMParser();
          const text =
            parser
              .parseFromString(value, 'text/html')
              ?.body.textContent?.trim() ?? '';
          if (text !== '') {
            return true;
          } else {
            return createError({ message: empty, path });
          }
        } catch (err) {
          return createError({
            message: invalid(err instanceof Error ? err : undefined),
            path,
          });
        }
      } else {
        return createError({ message: empty, path });
      }
    });
  },
);

declare module 'yup' {
  interface StringSchema {
    validCssSelector(): this;
    validNonEmptyHtml(opts?: ValidNonEmptyHtmlOpts): this;
    validRegex(): this;
    validXpathSelector(): this;
  }
}

export default yup;
