import {
  Button,
  ButtonProps,
  ClickAwayListener,
  MenuItem,
  MenuList,
  Paper,
  Popper,
} from '@material-ui/core';
import React from 'react';

import { Account } from '../../../api/src/domain/Account';

export type AccountDropdownButtonProps = {
  account: Account;
  buttonProps?: Omit<ButtonProps, 'children' | 'ref'>;
  onSignOut: () => void;
};

export const AccountDropdown: React.FC<AccountDropdownButtonProps> = ({
  account,
  buttonProps,
  onSignOut,
}) => {
  const anchorEl = React.useRef<HTMLButtonElement>(null);
  const [open, setOpen] = React.useState(false);
  return (
    <>
      <Button
        color="inherit"
        {...buttonProps}
        onClick={(ev) => {
          setOpen(true);
          buttonProps?.onClick?.(ev);
        }}
        ref={anchorEl}
      >
        {account.email}
      </Button>
      <Popper anchorEl={anchorEl.current} disablePortal open={open}>
        <Paper>
          <ClickAwayListener onClickAway={() => setOpen(false)}>
            <MenuList>
              <MenuItem>
                <Button onClick={() => onSignOut()}>Sign Out</Button>
              </MenuItem>
            </MenuList>
          </ClickAwayListener>
        </Paper>
      </Popper>
    </>
  );
};
